@media screen and (min-width: 320px) and (max-width: 767px) {
  .header {
    z-index: 100;
    position: fixed;

    width: 100%;
    height: 50px;
    box-shadow: 0 0 10px 0 #585454;

    background-color: rgba(0, 0, 0, 0.1);
    background-color: transparent;
  }
  .wrap {
    position: relative;
    width: 1100px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
  }
  .logo {
    width: 156px;
    margin: -20px;
    height: 95px;
    cursor: pointer;
  }
  .contentWrap {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: opacity 0.5s ease 0 1;
  }
  .burgerBtn {
    background-color: transparent;
    display: flex;
    align-items: center;
    padding: 0;
    width: 50px;
    height: 50px;
    cursor: pointer;
    border: none;
  }
  .burger {
    fill: white;
    width: 40px;
    height: 40px;
  }
  .navList {
    position: absolute;
    top: 0px;
    left: 50%;
    width: 700px;
    height: 50px;
    display: flex;
    transform: translateX(-50%);
    align-items: center;
    justify-content: center;

    border-top: none;
  }
  .navItem {
    padding: 0px 25px;
    cursor: pointer;
    transition: all 0.1s linear;
    width: 150px;
  }
  .navItem:hover {
    transform: scale(1.1);
    transition: all 0.1s linear;
  }
  .navLink {
    color: white;
    font-size: 20px;
    font-weight: 600;
    width: 100%;
    z-index: 305;

    /* padding-left: 30px; */
    text-align: center;
  }
  .navLinkActive {
    color: white;
    font-size: 20px;
    font-weight: 600;
    z-index: 305;
    color: #f5c946;
    /* padding-left: 30px; */
  }

  .nextBtn {
    display: flex;
    align-items: center;
    height: 75%;
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    color: white;
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s linear;
  }
  .nextBtn:hover {
    color: wheat;
    transition: all 0.3s linear;
  }
  .nextIcon {
    margin-left: 5px;
    fill: white;
    width: 30px;
    height: 30px;
    transform: rotate(90deg);
  }
  
}

